body {
   text-align: center;
   font-size: 20px;
   padding: 2.5%;
   font-family: RobotoCondensed;
}

h1, h2, h3, h4, h5 {
  font-family: EconomicaBold;
}

@font-face {
  font-family: RobotoCondensed;
  src: url(assets/roboto-condensed.light.ttf);
  font-weight: 800;
}

@font-face {
  font-family: EconomicaBold;
  src: url(assets/economica-bold.ttf);
}

.profile {
  width: 200px;
  height: 200px;
  border-radius: 100px;
}

.title-fade-out {
  opacity:0;
  animation-name: fade-out;
  animation-duration: 2s;
}

.title-fade-in {
  opacity:100;
  animation-name: fade-in;
  animation-duration: 2s;
}

@keyframes fade-out {
  from {opacity: 100}
  to {opacity: 0}
}

@keyframes fade-in {
  from {opacity: 0}
  to {opacity: 100}
}


@keyframes fade-in-out {
  from {opacity: 0}
  50% {opacity:100}
  to {opacity: 0}
}

.title-fade-in-out {
  opacity:0;
  animation-name: fade-in-out;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}
